:root {
  --body-bg: var(--bs-light);
  --body-color: var(--bs-dark);
  --primary-color: #3585fa;
  --focus-ring: #0d6efd40 0px 0px 0px .25rem;
  --skeleton-color: var(--bs-gray-300);
}

@media (prefers-color-scheme: dark) {
  :root {
    --body-bg: var(--bs-dark);
    --body-color: var(--bs-gray-100);
    --primary-color: var(--bs-info);
    --focus-ring: #0dcaf080 0px 0px 0px .2rem;
    --skeleton-color: var(--bs-gray-600);
  }

  .card, .list-group-item {
    background-color: var(--bs-gray-800);
    color: var(--body-color);
    border-color: var(--bs-gray-700);
  }

  .form-control, .form-control:focus, .input-group-text {
    background-color: var(--bs-gray-700);
    color: var(--body-color);
    border-color: #51555c;
  }

  .input-group-text {
    background-color: var(--bs-gray-800);
  }

  .form-control::placeholder {
    color: #adb5bdcc;
  }

  .modal-header {
    border-color: var(--bs-gray-700);
  }

  .btn.btn-primary, .btn.btn-primary:hover, .btn.btn-primary:focus, .btn-outline-primary:hover, .btn-outline-primary.active {
    color: var(--bs-dark);
  }

  .text-danger {
    color: #e05260 !important;
  }

  .text-muted {
    color: var(--bs-gray-500) !important;
  }

  code {
    color: #ff70b7;
  }
}

body {
  background-color: var(--body-bg);
  color: var(--body-color);
}

[hidden] {
  display: none !important;
}

dialog {
  background-color: var(--body-bg);
  border: 0;
  padding: 0;
}

a, a:hover, .btn-link, .btn-link:hover {
  color: var(--primary-color);
  text-decoration: none;
}

@media screen and (hover: hover) {
  a:hover, .btn-link:hover {
    text-decoration: underline;
  }
}

.btn.btn-default, .btn.btn-default:hover {
  color: var(--body-color);
}

.btn.btn-primary, .btn.btn-primary:focus, .btn.btn-primary:hover {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

.btn-outline-primary {
  border-color: var(--primary-color);
  color: var(--primary-color);
}

.btn-outline-primary:hover, .btn-outline-primary.active {
  border-color: var(--primary-color);
  background-color: var(--primary-color);
}

.btn:focus, .form-control:focus {
  box-shadow: var(--focus-ring);
}

.text-primary {
  color: var(--primary-color) !important;
}

.hide-scrollbars {
  scrollbar-color: transparent transparent;
  scrollbar-width: none;
}

.hide-scrollbars::-webkit-scrollbar {
  display: none;
}

